<template>
    <div class="Reportes" id="reporte">
        <h1>Bienvenido Administrador!</h1>
        <h4 style="text-align:center;">Elige que reporte deseas generar:</h4>
        <div style="text-align:center;min-width:100%;">
            <a class="bn39" href="/reportesrepartoscli"><span class="bn39span">Repartos</span></a>
            <a class="bn39" href="/reportesclientes"><span class="bn39span">Clientes</span></a>
            <a class="bn39" href="/reportesventas"><span class="bn39span">Ventas</span></a>
            <a class="bn39" href="/reportesproductos"><span class="bn39span">Productos</span></a>
            <a class="bn39" href="/reporteliquidacion"><span class="bn39span">Liquidacion</span></a>
        </div>
        <div class="module-border-wrap">
            <div class="module">
                <div style="width:30%;display:inline-block;padding:1em;border:1px solid black;border-radius:1em;">
                    <b-form-group label="*Reparto"
                                  label-for="id_reparto"
                                  @submit.stop.prevent="handleSubmit">
                        <v-autocomplete id="id_reparto"
                                        v-model="idrepaus"
                                        @change="cambiarrepausado(idrepaus)"
                                        type="text"
                                        placeholder="Ingrese un Reparto"
                                        invalid-feedback="Complete este campo"
                                        required
                                        solo
                                        filled
                                        :items="op_repartos">
                        </v-autocomplete>
                    </b-form-group>
                    <button class="button-64" id="botonpollo" @click="elegirtipo('Pollo')" role="button"> <span class="text"> Pollo</span> </button>
                    <button class="button-64" id="botonconge" @click="elegirtipo('Congelados')" role="button"> <span class="text">Conge</span> </button>
                    <button class="button-64" id="botonambos" @click="elegirtipo('Ambos')" role="button"> <span class="text">Ambos</span> </button>
                    <b-form-group label="Fecha Inicial" label-for="fechainiusada" style="display:inline-block;width:50%;">
                        <b-form-input id="fechainiusada"
                                      v-model="fechainiusada"
                                      type="date"
                                      @change="fechausadacambio()"
                                      placeholder="Ingrese la fecha en la que quiere el reporte"
                                      invalid-feedback="Complete este campo"
                                      required>
                        </b-form-input>
                    </b-form-group>
                    <b-form-group label="Fecha Final" label-for="fechafinusada" style="display:inline-block;width:50%;">
                        <b-form-input id="fechafinusada" gma
                                      v-model="fechafinusada"
                                      type="date"
                                      @change="fechausadacambio()"
                                      placeholder="Ingrese la fecha en la que quiere el reporte"
                                      invalid-feedback="Complete este campo"
                                      required>
                        </b-form-input>
                    </b-form-group>
                </div>
                <div style="width:30%;display:inline-block;vertical-align:top;text-align:center;">
                    <p style="margin-top:-1em; margin-bottom:-1em;">Suma de Ventas</p>
                    <div style="border:1px solid black;text-align:center; padding:1em;border-radius:1em;margin:1em;">
                        <b>${{sumadeventas}}</b>
                    </div>
                    <p style="margin-top:-1em; margin-bottom:-1em;">Total Cobrado</p>
                    <div style="border:1px solid black;text-align:center;padding:1em;border-radius:1em;margin:1em;">
                        <b>${{totalcobrado}}</b>
                    </div>
                </div>
                <div style="width:10%;display:inline-block;vertical-align:top;">
                    <p style="margin-top:-1em; margin-bottom:-1em;vertical-align:top;text-align:center;">Efectivo</p>
                    <div style="border:1px solid black;text-align:center; padding:1em;border-radius:1em;margin:1em;">
                        <b>${{totalEfectivo}}</b>
                    </div>
                    <p style="margin-top:-1em; margin-bottom:-1em;vertical-align:top;text-align:center;">Merc Pago</p>
                    <div style="border:1px solid black;text-align:center;padding:1em;border-radius:1em;margin:1em;">
                        <b>${{totalMercadoPago}}</b>
                    </div>
                </div>
                <div style="width:30%;display:inline-block;vertical-align:top;text-align:center;">
                    <div style="display:inline-block;text-align:center;vertical-align:top;"><canvas id="chart2" width="150" height="150" style="text-align:center;display:inline-block;"></canvas></div>
                    <div style="display:inline-block;text-align:center;vertical-align:top;"><canvas id="chart" width="150" height="150" style="text-align:center;display:inline-block;"></canvas></div>
                </div>
                <b-modal no-close-on-backdrop size="xl"
                         ref="modal-pdf"
                         id="modal-pdf"
                         hide-footer>
                    <template #modal-title>
                        <h5 class="modal-title">Vista Previa</h5>
                    </template>
                    <reporte-listadopdf :PDFventa="ordenAPDF"></reporte-listadopdf>
                </b-modal>
                <component is="script" src="https://code.jquery.com/jquery-1.12.3.min.js" async></component>
                <component is="script" src="https://cdnjs.cloudflare.com/ajax/libs/jspdf/0.9.0rc1/jspdf.min.js" async></component>
                <component is="script" src="https://cdn.jsdelivr.net/npm/chart.js@2.9.4" async></component>
                <div style="width:30%;display:inline-block;margin-right:2em;vertical-align:top;">
                    <b-table :fields="fields"
                             striped
                             sortable
                             responsive
                             :no-border-collapse="false"
                             hover
                             :items="tabla_clientes"
                             show-empty
                             :per-page="perPage"
                             :current-page="currentPage"
                             ref="tablaregistros"
                             id="tablaregistros">
                        <template #empty="">
                            <b>No hay registros para mostrar</b>
                        </template>

                        <template #cell(selected)="{ rowSelected }">
                            <template v-if="rowSelected">
                                <span aria-hidden="true">&check;</span>
                                <span class="sr-only">Selected</span>
                            </template>
                            <template v-else>
                                <span aria-hidden="true">&nbsp;</span>
                                <span class="sr-only">Not selected</span>
                            </template>
                        </template>

                        <template slot="cell(numero_cliente)" slot-scope="data">
                            <b>{{ data.value }}</b>
                        </template>

                        <template slot="cell(nombre)" slot-scope="data">
                            {{ data.value.toUpperCase() }}
                        </template>

                        <template slot="cell(saldototal)" slot-scope="row">
                            {{ row.item.cliente.saldopollo + row.item.cliente.saldocongelados}}
                        </template>

                        <template slot="cell(id_reparto)" slot-scope="data">
                            {{ data.value.split('/')[4]}}
                        </template>

                        <template slot="cell(visitado)" slot-scope="row">
                            <div v-if="row.item.tabla_ventas.length > 0">
                                <svg xmlns="http://www.w3.org/2000/svg"
                                     width="16"
                                     height="16"
                                     fill="currentColor"
                                     class="bi bi-check-circle-fill"
                                     viewBox="0 0 16 16"
                                     style="color: green">
                                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z" />
                                </svg>
                                SI
                            </div>
                            <div v-else>
                                <svg xmlns="http://www.w3.org/2000/svg"
                                     width="16"
                                     height="16"
                                     fill="currentColor"
                                     class="bi bi-x-circle-fill"
                                     viewBox="0 0 16 16"
                                     style="color: red">
                                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z" />
                                </svg>
                                NO
                            </div>
                        </template>

                        <template slot="cell(provincia)" slot-scope="data">
                            {{ data.value.toUpperCase() }}
                        </template>
                    </b-table>
                </div>
                <div style="width:60%;display:inline-block;vertical-align:top;text-align:center;margin-top:-9em;">
                    <b-table :fields="fieldsstock"
                             stacked
                             sortable
                             responsive
                             hover
                             show-empty
                             :items="tabla_stocks"
                             :sticky-header="false"
                             :no-border-collapse="false"
                             :per-page="perPage"
                             :current-page="currentPage"
                             ref="tablaregistros2"
                             id="tablaregistros2"
                             selectable
                             select-mode="multi"
                             @row-selected="seleccionar_una"
                             empty-text="No hay ningún stock definido para este reparto en las fechas"
                             empty-filtered-text="No hemos encontrado registros que coincidan con lo que está buscando">
                        <template #cell(selected)="{ rowSelected }">
                            <template v-if="rowSelected">
                                <span aria-hidden="true">&check;</span>
                                <span class="sr-only">Selected</span>
                            </template>
                            <template v-else>
                                <span aria-hidden="true">&nbsp;</span>
                                <span class="sr-only">Not selected</span>
                            </template>
                        </template>

                        <template slot="cell(id_reparto)" slot-scope="data">
                            {{ data.value.split('/')[4]}}
                        </template>

                        <template slot="cell(componentes)" slot-scope="data" class="buenardo">
                            <b-card title="Productos: " v-if="data.value.length>0" style="width:100%;">

                                <b-list-group vertical style="width:100%;">
                                    <div v-for="componente in data.value"
                                         :key="componente.id">
                                        <b>Producto:</b>
                                        {{
productos.find(s=>{
                                                                    return s.id_producto == componente.producto.split('/')[4]
                                                                }).nombre
                                        }} - <b>Cant.: </b>
                                        {{componente.tipo==2?"$":""}}{{componente.cantidad}}
                                        <br />
                                        <br />

                                    </div>
                                </b-list-group>
                            </b-card>
                            <b-list-group-item v-else>
                                Sin Productos
                            </b-list-group-item>
                        </template>

                        <template slot="cell(cerrado)" slot-scope="data">
                            <div v-if="data.value === true">
                                <svg xmlns="http://www.w3.org/2000/svg"
                                     width="16"
                                     height="16"
                                     fill="currentColor"
                                     class="bi bi-check-circle-fill"
                                     viewBox="0 0 16 16"
                                     style="color: green">
                                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z" />
                                </svg>
                                SI
                            </div>
                            <div v-else>
                                <svg xmlns="http://www.w3.org/2000/svg"
                                     width="16"
                                     height="16"
                                     fill="currentColor"
                                     class="bi bi-x-circle-fill"
                                     viewBox="0 0 16 16"
                                     style="color: red">
                                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z" />
                                </svg>
                                NO
                            </div>
                        </template>

                        <template #row-details="row">
                            <b-card>
                                <b-list-group horizontal>
                                    <b-list-group>
                                        <b-list-group-item>
                                            <b>Nombre del Stock:</b>
                                            {{ row.item.nombre }}
                                        </b-list-group-item>
                                        <b-card title="Productos: " v-if="row.item.componentes.length>0">

                                            <b-list-group horizontal>
                                                <div v-for="componente in row.item.componentes"
                                                     :key="componente.id">

                                                    <b-list-group>
                                                        <b-list-group-item>
                                                            <b>Producto:</b>
                                                            {{
productos.find(s=>{
                                                                    return s.id_producto == componente.producto.split('/')[4]
                                                                }).nombre
                                                            }}
                                                        </b-list-group-item>
                                                        <b-list-group-item>
                                                            <p>

                                                                <b>Cantidad: </b>
                                                                {{componente.tipo==2?"$":""}}{{componente.cantidad}}
                                                            </p>
                                                        </b-list-group-item>
                                                    </b-list-group>

                                                </div>
                                            </b-list-group>
                                        </b-card>
                                        <b-list-group-item v-else>
                                            Sin Productos
                                        </b-list-group-item>

                                    </b-list-group>
                                    &nbsp;
                                </b-list-group>
                            </b-card>
                        </template>
                    </b-table>
                </div>
            </div>
            </div>
    </div>
</template>

<script>
let api = new URL("http://93.188.162.174:8081");
api.pathname = "clientes";

import VueHtml2pdf from "vue-html2pdf";
import { mapState, mapActions } from "vuex";
import axios from "axios";
import "jspdf/dist/polyfills.es.js";
import { jsPDF } from "jspdf";
import Sortable, { MultiDrag, Swap } from 'sortablejs';
import Buefy from 'buefy';
import html2pdf from 'html2pdf.js'
import Chart from 'chart.js/auto';
import { getRelativePosition } from 'chart.js/helpers';
import { APIControler } from "@/store/APIControler";
import ElTableDraggable from 'el-table-draggable'
export default {
    name: 'HelloWorld',
    data () {
        return {
            repartoslista: [],
            reparto: {},
            chart: null,
            tabla_stocks: [],
            chart2: null,
            chart3: null,
            datarep: [],
            listacli: [],
            hex: [],
            tabla_clientes:[],
            idrepaus: 0,
            lista_id: [],
            tipoelegido: 'Ambos',
            repartousado: {
                repa: {},
                lista_id: [],
                tabla_clientes: [],
            },
            productos: [],
            ventasepicas: [],
            chart: null,
            chart2: null,
            datachart: {
                labels: [ 'Pollo', 'Congelados'],
                datasets: []
            },
            datachart2: {
                labels: [
                ],
                datasets: []
            },
            stocks: [],
            fields: [
                { key: "cliente.numero_cliente", label: "N° Cliente", sortable: true},
                { key: "cliente.nombre", label: "Nombre/s", sortable: true },
                { key: "saldototal", label: "Saldo Actual", sortable: true },
                { key: "visitado", label: "Visitado", sortable: true },
                //{ key: 'yavisitado', label: 'Ya Visitado', sortable: true, },
                //{ key: "action", label: "Acciones", variant: "secondary" },
            ],
            fieldsstock: [
                { key: "id_stock", label: "ID", sortable: true },
                { key: "nombre", label: "Nombre", sortable: true },
                { key: "fechastock", label: "Fecha", sortable: true },
                { key: "id_reparto", label: "ID Reparto", sortable: true },
                { key: "cerrado", label: "Stock Cerrado", sortable: true },
                { key: "componentes", label: "", sortable: true },
            ],
            totalRows: 1, //Total de filas
            currentPage: 1, //Pagina actual
            perPage: 10, // Datos en la tabla por pagina
            buscar: "",
            op_repartos: [],
            fechainiusada: null,
            fechafinusada: null,
            sortableOptions: {
                chosenClass: 'is-selected'
            },
            token: "",
            infoVisitar: {
                id: "modal_visitar",
                cliente: -1,
            },
            totalEfectivo: 0,
            totalMercadoPago: 0,
            sumadeventas: 0,
            totalcobrado: 0,
            ordenAPDF: {}, //Se carga cuando se hace clic en exportar a pdf, con la orden a exportar
        };
    },
    computed: {
        rows() {
          return this.tabla_ventas.length;
        },
        id() {
            return this.tabla_ventas.numero_orden;
        },
        items() {
          return tabla_ordenes.filter((item) => {
            return item.numero_orden
              .toLowerCase()
              .includes(this.buscar.toLowerCase());
          });
        },
    },
    methods: {
            suma(ventas)
        {
              let suma = 0;
              for (var i = 0; i < ventas.length; i++)
              {
                  suma += ventas[i].totalpagado + ventas[i].totalpagadomp;
              }
              return suma;
        },
        costos(ventas) {
            let costotot = 0;
            ventas.forEach((venta) => {
                costotot = costotot + venta.costototal
            });
            return costotot;
        },
        ganancia(ventas) {
            let costotot = 0;
            ventas.forEach((venta) => {
                costotot = costotot + venta.ganancia
            });
            return costotot;
        },
        cantidad(ventas) {
            let costotot = 0;
            ventas.forEach((venta) => {
                venta.componentes.forEach((prod) =>
                {
                    costotot = costotot + prod.cantidad;
                })
            });
            return costotot;
        },
        saldos(ventas) {
            let costotot = 0;
            ventas.forEach((venta) => {
                costotot = costotot - venta.diferencia;
            });
            return Math.abs(costotot);
        },
        fechausadacambio()
        {
            //this.setGraph();
            if (this.idrepaus != -1) {
                this.testFetch();
                this.testFetch3();
                this.testFetch5();
            } else
            {
                this.testFetch2();
            }
        },
        cambiarrepausado(id)
        {
            if (this.idrepaus != -1) {
                this.repartousado.repa = this.getReparto(id);
                //this.setGraph();
                this.testFetch();
                this.testFetch3();
                this.testFetch5();
            } else {
                this.repartousado.repa = null;
                //this.setGraph();
                this.testFetch2();
            }
        },
        rowClass(item, type) {
            if (!item || type !== 'row')
            {
                return 'table-danger'
            }
            if ((item.cliente.saldopollo + item.cliente.saldocongelados) > this.getLimite(item.cliente.categoria.split('/')[4]).limite)
            {
                return 'table-warning'
            }
        },
        checkeartipo()
        {
            if (this.tipoelegido == 'Pollo') {
                document.getElementById("botonpollo").className = "button-642";
                document.getElementById("botonconge").className = "button-64";
                document.getElementById("botonambos").className = "button-64";
            } else
            {
                if (this.tipoelegido == 'Ambos') {
                    document.getElementById("botonpollo").className = "button-64";
                    document.getElementById("botonconge").className = "button-64";
                    document.getElementById("botonambos").className = "button-642";
                } else
                {
                    document.getElementById("botonpollo").className = "button-64";
                    document.getElementById("botonconge").className = "button-642";
                    document.getElementById("botonambos").className = "button-64";
                }
            }
        },
        iconos(item)
        {
            let icono = '';
            let maximosaldo = 0;
            let maximoventa = 0;
            let maximoganancia = 0;
            this.repartousado.tabla_clientes.forEach((element) => {
                if (this.saldos(element.tabla_ventas) > maximosaldo) {
                    maximosaldo = this.saldos(element.tabla_ventas);
                }
                if (this.suma(element.tabla_ventas) > maximoventa) {
                    maximoventa = this.suma(element.tabla_ventas);
                }
                if (this.ganancia(element.tabla_ventas) > maximoganancia) {
                    maximoganancia = this.ganancia(element.tabla_ventas);
                }
            })
            if (this.ganancia(item.tabla_ventas) == maximoganancia) {
                icono = icono + ' ＄ ';
            }
            if (this.suma(item.tabla_ventas) == maximoventa) {
                icono = icono + ' ★ ';
            }
            if (this.saldos(item.tabla_ventas) == maximosaldo) {
                icono = icono + ' ✗ ';
            }
            return icono;
        },
        async testFetch5() {
            try {
                this.tabla_stocks = [];
                let id_sto = this.repartousado.repa.id_reparto;
                this.stocks.forEach((stock) => {
                    if (stock.id_reparto.split('/')[4] == id_sto) {
                        if (this.checkearfecha(stock.fechastock))
                        {
                            this.tabla_stocks.push(stock)
                        }
                    }
                })

            } catch (error) {
                console.log(error);
            }
        },
        getLimite(idcat)
        {
            let categ = this.listacat.find(s => {
                return s.id_categoria == idcat
            })
            return categ;
        },
        async getStocks() {
            let serviciosAPI = new APIControler();
            serviciosAPI.apiUrl.pathname = "stocks/";
            this.stocks = await serviciosAPI.getData()
        },
        async getCategorias() {
            let productosAPI = new APIControler();
            productosAPI.apiUrl.pathname = "categorias/";
            this.listacat = await productosAPI.getData();
        },
        async testFetch3() {
            try {
                const res = await fetch(api);
                const data = await res.json();
                let totalventas = data.results.length;
                this.datachart.datasets = [];
                this.datachart2.datasets = [];
                let efectivopollo = 0
                let efectivoconge = 0
                let mercadopagopollo = 0
                let mercadopagoconge = 0
                let sumadeventaspollo = 0
                let sumadeventasconge = 0
                let totalcobradopollo = 0
                let totalcobradoconge = 0
                let totalpollo = 0
                let totalconge = 0
                this.datachart2.labels = [];
                let datasetear = {
                    label: 'Ganancia por Reparto',
                    data: [],
                    backgroundColor: [
                        'rgb(255, 205, 86)',
                        'rgb(137, 30, 53)',
                    ],
                    hoverOffset: 4
                };
                let datasetear2 = {
                    label: 'Ventas por Reparto',
                    data: [],
                    backgroundColor: [
                        'rgb(0, 0, 0)',
                        'rgb(255, 165, 0)',
                        'rgb(137, 30, 53)',
                    ],
                    hoverOffset: 4
                };
                let repa = this.repartousado.repa
                this.datachart2.labels.push('reparto ' + repa.id_reparto);
                let opcionrepacopia = {
                    id_reparto: -1,
                    lista_id: [],
                    tabla_ventas: [],
                };
                opcionrepacopia.id_reparto = repa.id_reparto
                this.ventasepicas.forEach((venta) => {
                    let fecharda = venta.fecharealizacion.split('-')[2] + venta.fecharealizacion.split('-')[1] + venta.fecharealizacion.split('-')[0]
                    if (this.checkearfecha(venta.fecharealizacion)) {
                        if (venta.id_reparto.split('/')[4] == repa.id_reparto) {
                            opcionrepacopia.tabla_ventas.push(venta)
                            if (venta.tipo_venta == 'Pollo') {
                                efectivopollo += venta.totalpagado
                                mercadopagopollo += venta.totalpagadomp
                                sumadeventaspollo += venta.gananciatotal
                                totalpollo++;
                            } else
                            {
                                efectivoconge += venta.totalpagado
                                mercadopagoconge += venta.totalpagadomp
                                sumadeventasconge += venta.gananciatotal
                                totalconge++;
                            }
                        }
                    }
                });
